@import url(https://fonts.googleapis.com/css2?family=Public+Sans:wght@300;400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  font-family: "Public Sans", sans-serif;
}
h1,
h2,
h4,
h5 {
  color: hsl(233, 26%, 24%);
  font-weight: 300;
}
h4 {
  font-weight: 700;
}
h5 {
  font-weight: 400;
  font-size: 20px;
}
h5:hover {
  color: hsl(136, 65%, 51%);
  cursor: pointer;
}
p {
  color: hsl(233, 8%, 62%);
  line-height: 1.5rem;
}
span {
  color: gray;
  font-size: 14px;
}

.Button {
  color: #ffffff;
  background: linear-gradient(0.25turn, hsl(136, 65%, 51%), hsl(192, 70%, 51%));
  padding: 10px 35px;
  border: none;
  border-radius: 21px;
  font-size: 14px;
  cursor: pointer;
  margin: 0;
}
.Button:focus {
  outline: none;
}

.NavBarLayout {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.NavBarMobile {
  text-align: center;
}
.NavBarMobile h1 {
  margin: 25px;
}



.Intro {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.Intro div {
  width: 300px;
  margin-bottom: 25px;
}
.Intro img {
  width: 500px;
  position: relative;
  top: -100px;
  z-index: 999;
}
.IntroBackground {
  background-color: hsl(0, 0%, 98%);
  background-image: url(/Easybank-Landing-Page/static/media/bg-intro-desktop.8be308fd.svg);
  background-position: 600px 50%;
  background-size: 90%;
  background-repeat: no-repeat;
  padding: 25px;
  transition: 700ms ease;
}
@media (min-width: 280px) and (max-width: 1000px) {
  .Intro {
    flex-direction: column-reverse;
    text-align: center;
  }
  .Intro img {
    width: 100%;
  }
  .IntroBackground {
    background-image: url(/Easybank-Landing-Page/static/media/bg-intro-mobile.c2ed38d0.svg);
    background-position: 5px -100px;
    background-repeat: no-repeat;
    background-size: 100%;
  }
}

.ChooseLayout {
  background-color: hsl(220, 16%, 96%);
  padding: 100px 75px;
}
.ChooseDescriptionLayout {
  display: flex;
  justify-content: flex-start;
}
.ChooseDescriptionLayout div {
  margin: 50px 15px;
}

@media (min-width: 280px) and (max-width: 1026px) {
  .ChooseLayout {
    text-align: center;
  }
  .ChooseDescriptionLayout {
    display: flex;
    flex-direction: column;
  }
}

.Articles {
  background-color: hsl(0, 0%, 98%);
  padding: 100px 75px;
}
.ArticlesLayout {
  display: flex;
}
.MappedLayout {
  background-color: white;
  border-radius: 5px;
  box-shadow: 0px 0px 3px 0px grey;
  margin: 20px 10px 50px 0;
  width: 75%;
}
.MappedLayout img {
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  width: 100%;
  height: 250px;
}
.MappedLayout div {
  padding: 20px;
}

@media (min-width: 280px) and (max-width: 1026px) {
  .Articles {
    background-color: hsl(0, 0%, 98%);
    padding: 100px 50px;
  }
  .Articles h2 {
    text-align: center;
  }
  .ArticlesLayout {
    display: block;
    flex-direction: column;
  }
  .MappedLayout {
    background-color: white;
    border-radius: 5px;
    margin: 20px 15px 50px 0;
    width: 100%;
  }
  .MappedLayout img {
    width: 100%;
    height: auto;
  }
}

.FooterLayout {
  background-color: hsl(233, 26%, 24%);
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  padding: 25px;
}
.FooterLayout p:hover {
  -webkit-filter: invert(57%) sepia(100%) saturate(691%) hue-rotate(84deg)
    brightness(106%) contrast(109%);
          filter: invert(57%) sepia(100%) saturate(691%) hue-rotate(84deg)
    brightness(106%) contrast(109%);
}
.SocialMediaIcons {
  display: flex;
}
.SocialMediaIcons img {
  margin: 10px 5px;
  padding-top: 25px;
}
.SocialMediaIcons img:hover {
  -webkit-filter: invert(57%) sepia(100%) saturate(691%) hue-rotate(84deg)
    brightness(106%) contrast(109%);
          filter: invert(57%) sepia(100%) saturate(691%) hue-rotate(84deg)
    brightness(106%) contrast(109%);
}
.ButtonFooter {
  text-align: right;
}
.ButtonFooter div {
  margin-bottom: 15px;
}

@media (min-width: 280px) and (max-width: 1000px) {
  .FooterLayout {
    background-color: hsl(233, 26%, 24%);
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    padding: 25px;
    text-align: center;
  }
  .ButtonFooter {
    text-align: center;
  }
}

